import { AnimLoopEngine } from 'anim-loop-engine';
import { GameOfLifeMatrix } from './GOLMatrix';
import { clearCells, drawBgGrid, drawLiveCells } from './GOLDrawingUtils';
import { getPreset } from './GOLPresets';
var engine = new AnimLoopEngine();
// Defaults, declarations and elements
var cellSize;
var lastMouseBtn = 0;
var mouseBtn = 0;
var running = false;
var loopInterval = 150;
var gridSize = 20;
var drawX = 0;
var drawY = 0;
var onChangeTimeoutId = 0;
var GOL = new GameOfLifeMatrix(gridSize);
var btnTextActive = 'PAUSE';
var btnTextInactive = 'RUN';
var btnToggle = document.getElementById('btn-toggle-sim');
var txtGridSize = document.getElementById('txt-grid-size');
var txtInterval = document.getElementById('txt-interval');
var bgCanvas = document.getElementById('g-o-l-bg');
var canvas = document.getElementById('g-o-l');
var bgCtx = bgCanvas.getContext('2d');
var ctx = canvas.getContext('2d');
// Assign defaults to control input fields
txtGridSize.value = gridSize.toString();
txtInterval.value = loopInterval.toString();
// >>> Setup canvas
var rect = canvas.getBoundingClientRect();
var canvasStyle = window.getComputedStyle(canvas);
var W = parseInt(canvasStyle.width);
var H = parseInt(canvasStyle.height);
bgCanvas.setAttribute('width', canvasStyle.width);
canvas.setAttribute('width', canvasStyle.width);
if (W < H) {
    bgCanvas.style.height = canvasStyle.width;
    canvas.style.height = canvasStyle.width;
    bgCanvas.setAttribute('height', canvasStyle.width);
    canvas.setAttribute('height', canvasStyle.width);
}
else {
    bgCanvas.setAttribute('height', canvasStyle.height);
    canvas.setAttribute('height', canvasStyle.height);
}
// Set grid cell size based on canvas width and number of divisions
cellSize = W / gridSize;
// >>> Game control events
// Run/pause button toggle
btnToggle.onclick = function () {
    if (!running) {
        start();
    }
    else {
        stop();
    }
};
// Grid size value change
txtGridSize.onkeyup = function (e) {
    clearTimeout(onChangeTimeoutId);
    onChangeTimeoutId = setTimeout(function () {
        var newGridSize = parseInt(e.target.value);
        if (newGridSize !== gridSize) {
            gridSize = newGridSize;
            cellSize = W / gridSize;
            GOL.gridSize = gridSize;
            clearCells(bgCtx, W, H);
            drawBgGrid(bgCtx, gridSize, cellSize);
        }
    }, 1000);
};
// Animation interval value change
txtInterval.onkeyup = function (e) {
    clearTimeout(onChangeTimeoutId);
    onChangeTimeoutId = setTimeout(function () {
        loopInterval = e.target.value;
    }, 1000);
};
// <<<  Game control events
// >>> Presets
var presetBtns = document.getElementsByClassName('btn-preset');
for (var i = 0; i < presetBtns.length; i++) {
    var btn = presetBtns[i];
    btn.onclick = function (e) {
        GOL.liveCells = getPreset(e.currentTarget.id.replace('preset-', ''), e.currentTarget.dataset.pad ? parseInt(e.currentTarget.dataset.pad) : null);
        clearCells(ctx, W, H);
        drawLiveCells(ctx, GOL.liveCells, cellSize);
    };
}
// <<< Presets
// Window blur stops animation, just in case
window.onblur = function () {
    stop();
};
// Handle cell clicks on the grid
var handleMouseDraw = function (e) {
    if (mouseBtn !== 0) {
        var gridX = Math.floor((e.clientX - rect.left) / cellSize);
        var gridY = Math.floor((e.clientY - rect.top) / cellSize);
        if (gridX !== drawX || gridY !== drawY || mouseBtn !== lastMouseBtn) {
            drawX = gridX;
            drawY = gridY;
            var state = mouseBtn < 0 ? 1 : 0;
            // Toggle a cell's alive state
            GOL.setCellState(gridX, gridY, state);
            clearCells(ctx, W, H);
            drawLiveCells(ctx, GOL.liveCells, cellSize);
        }
        lastMouseBtn = mouseBtn;
    }
};
canvas.addEventListener('contextmenu', function (e) { return e.preventDefault(); });
canvas.onmousedown = function (e) {
    mouseBtn = e.button === 0 ? -1 : 1;
    handleMouseDraw(e);
};
canvas.onmousemove = handleMouseDraw;
window.onmouseup = function () { return (mouseBtn = 0); };
// Game update loop, receives timestamp from AnimLoopEngine
var tsFrom = 0;
var update = function (ts) {
    if (ts === void 0) { ts = 0; }
    if (ts - tsFrom >= loopInterval) {
        GOL.iterate();
        clearCells(ctx, W, H);
        drawLiveCells(ctx, GOL.liveCells, cellSize);
        tsFrom = ts;
    }
};
// Start/stop
var start = function () {
    engine.start();
    btnToggle.innerHTML = btnTextActive;
    running = true;
};
var stop = function () {
    engine.stop();
    btnToggle.innerHTML = btnTextInactive;
    running = false;
};
// Draw the BG grid
drawBgGrid(bgCtx, gridSize, cellSize);
// Add update task to engine
engine.addTask(update);
// Everything is initialized
