var bgColor = '#DDD';
// Draw a single cell
var drawBgCell = function (ctx, x, y, cellSize) {
    ctx.beginPath();
    ctx.rect(x * cellSize, y * cellSize, cellSize, cellSize);
    ctx.fill();
};
// Clear canvas
export var clearCells = function (ctx, W, H) {
    ctx.clearRect(0, 0, W, H);
};
// Generate background grid
export var drawBgGrid = function (ctx, gridSize, cellSize) {
    ctx.fillStyle = bgColor;
    for (var i = 0; i < gridSize; i++) {
        for (var j = 0; j < gridSize; j++) {
            if (i % 2 === 0) {
                if (j % 2 === 0) {
                    drawBgCell(ctx, i, j, cellSize);
                }
            }
            else {
                if (j % 2 !== 0) {
                    drawBgCell(ctx, i, j, cellSize);
                }
            }
        }
    }
};
// Draw live cells
export var drawLiveCells = function (ctx, liveCells, cellSize) {
    ctx.fillStyle = '#484';
    for (var i in liveCells) {
        for (var j in liveCells[i]) {
            var numI = parseInt(i);
            var numJ = parseInt(j);
            drawBgCell(ctx, numJ, numI, cellSize);
        }
    }
};
